body {
  background-color: #ffffff !important;
  max-width: 100vw;
}
.adminBody {
  background-color: #ffffff;
  height: 40vh;
  overflow: hidden;
  max-width: 100vw;
}
.adminContainer {
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: #ffffff;
  z-index: 1;
}

.AdminContent {
  padding: 30vh 20vw;
}

.deletePost {
  color: rgb(0, 0, 0);
  font-size: large;
}

.postHeader .title h1 {
  background-color: rgb(194, 194, 194);
}

.adminTitle {
  background-color: #ffffff;
}

.adminContainer .adminBody {
  color: rgb(0, 0, 0);
  font-size: 1.4rem;
}

@media (max-width: 900px) {
  .AdminContent {
    padding: 20vh 5vw;
  }
}

.postContainer,
.adminPostTitle,
.post,
.postHeader,
.title {
  background-color: white;
}

.postContainer {
  padding: 4vh 10vw;
  border-radius: 10px;
}

.adminPostTitle {
  text-align: center;
  font-weight: 700;
  margin-bottom: 10vh;
  background-color: #ffffff;
}
.adminContainer .postContainer {
  padding: 5vh;
  border: 5px solid rgb(0, 0, 0);
}

.post {
  border-bottom: 5px solid black;
  padding: 2vw;
}

.adminContainer .post,
.adminContainer .postHeader,
.adminContainer .title,
.adminContainer .deletePost,
.adminContainer .postbody,
.adminContainer pre {
  background-color: rgb(194, 194, 194);
  max-width: 100vw;
}
.postbody pre {
  font-size: 1.5 rem !important;
}

.adminUsers {
  max-width: 100vw;
}

.adminUsersContainer {
  width: 80vw;
  max-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2fabf0;
}

.adminUserAdd {
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  min-height: 300px;
  max-width: 100vw;
  align-items: center;
  justify-content: space-between;
  background-color: #2fabf0;
}

.adminUserAddItem {
  width: 50vw;
  height: auto;
  min-height: 30px;
  font-size: 20px;
  max-width: 100vw;
}

.adminUserAddButton {
  width: 200px;
  height: 50px;
  font-size: 28px;
  font-weight: 600;
}

.AdminUsersList {
  background-color: white;
  color: black;
  margin-left: 5vw;
  margin-bottom: 20vh;
  max-width: 100vw;
}

.AdminUsersList h1 {
  text-align: center;
  max-width: 100vw;
}

.UserDataContainer {
  width: 100%;
  max-width: 100vw;
}

.UserDataHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  max-width: 100vw;
}

.UserDisplay {
  display: flex;
  flex-direction: row;
  width: 70vw;
  align-items: center;
  justify-content: space-around;
}

.searchBox {
  margin: 4rem;
  width: 50vw;
  height: 3rem;
  font-size: 1rem;
}

@media (min-width: 900px) {
  .adminUsersContainer {
    min-width: 100vh;
    width: 80vh;
    min-height: 100vh;
    padding: 20vh 0vw 0vh 50vw;
  }
}

@media (max-width: 900px) {
  .adminUsersContainer {
    width: 80vh;
    max-width: 80vh;
    min-height: 100vh;
    padding: 0;
  }
  .UserDisplay {
    display: flex;
    flex-direction: column;
    width: 70vw;
    align-items: flex-start;
    justify-content: space-around;
    border: 3px solid black;
  }
  .searchBox {
    margin: 2rem 5rem;
  }
  .adminUsersContainer {
    align-items: flex-start;
  }
  .UserDataHeader {
    align-items: flex-start;
    margin-left: 5vw;
  }
  .adminUserAdd {
    padding: 10px 5rem;
  }
}

.adminUserPage {
  background-color: #2fabf0;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}

.AdminDeleteUser {
  background-color: white;
  border: none;
  font-size: large;
}
.AdminDeleteUser:hover {
  cursor: pointer;
  color: red;
}
