body {
  background-color: white;
}
.createPostPage {
  width: 100vw;
  height: 90vh;
  padding: 0 10vw;
  display: grid;
  place-items: center;
}

.cpContainer {
  width: 100%;
  height: 100%;

  background-color: #ffffff;
  color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.cpContainer h1 {
  text-align: center;
  align-self: flex-start;
}

.cpContainer label {
  font-size: 25px;
}
.cpContainer .inputGp {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.inputGp {
  width: 100%;
}

.inputGp input,
.inputGp textarea {
  font-size: 18px;
  border: none;
  border-radius: 2px;
  padding: 5px;
}

.inputGp input {
  height: 40px;
  width: 100%;
}
.inputGp textarea {
  height: 150px;
  width: 100%;
}

.cpContainer button {
  align-self: center;
  margin-top: 20px;
  height: 40px;
  width: 50vw;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  background-color: #2fabf0;
  color: white;
}

.cpContainer button:hover {
  background-color: #1a4973;
  font-size: 20px;
}
input,
textarea {
  margin-top: 5px;
}
