body {
  background-color: #2fabf0 !important;
}
.navbarSide {
  overflow-x: hidden;
  background-color: #2fabf0;
  position: fixed;
  display: flex;
  flex-direction: column;
  color: rgb(0, 0, 0);
  justify-content: space-around;
  align-items: center;
  height: 100%;
  min-height: 100vh;
}
.navItem {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  background-color: #2fabf0;
}

.navItemTitle {
  color: rgb(0, 0, 0);
  font-size: 1.4rem;
  font-weight: 600;
  background-color: #2fabf0;
  width: 100%;
  text-align: center;
}
.navItemTitle:hover {
  color: rgb(31, 31, 31);
  font-size: 1.5rem;
  font-weight: 600;
}

.navItem button {
  background-color: rgb(30, 33, 33);
  color: white;
  font-size: 30px;
  box-shadow: none;
  border: none;
  border-radius: 5px;
}
.navItem button:hover {
  cursor: pointer;
  background-color: rgb(24, 24, 24);
}

@media (min-width: 900px) {
  .navbarSide {
    width: 15vw;
    min-width: 150px;
    border-right: 5px solid black;
  }
  .bars {
    display: none;
  }
}
@media (max-width: 900px) {
  .navbarSide {
    width: 100%;
    min-width: 100vw;
    max-width: 100vw;
  }
  .bars i {
    display: block;
    width: 100vw;
  }
}

.bars {
  background-color: #2fabf0;
  color: white;
  width: 15vw;
  min-width: 150px;
  text-align: center;
  font-size: 2rem;
  padding-top: 10px;
}
.bars i {
  background-color: #2fabf0;
}

.SidebarContainer {
  background-color: #2fabf0;
  max-width: 100vw;
}
