body {
  background-color: #ffffff !important;
}
.SignupContainer {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 500px;
  width: 100%;
  margin: auto;
  background-color: white;
}
.SignupContainer > form {
  width: 70%;
  border: 1px solid #dfdfdf;
  padding: 2rem;
  border-radius: 5px;
  color: white;
  background-color: #003653 !important;
}
.SignupField > label,
.SignupField p {
  color: white !important;
  background-color: #003653 !important;
}
.SignupContainer form button {
  background: #0563b4;
  border-radius: 0 !important;
}
.SignupContainer form p {
  color: red;
  background-color: #003653 !important;
}

.SignupContainer form pre {
  font-size: large;
  background-color: #003653 !important;
}

.SignupContainer form h1,
.SignupContainer .field,
.SignupContainer input {
  background-color: #003653 !important;
}
.SignupContainer input {
  color: #ffffff !important;
  background-color: #003653 !important;
}

.LoginContainer {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 500px;
  width: 100%;
  margin: auto;
  background-color: white;
}
.LoginContainer form {
  width: 70%;
  border: 1px solid #dfdfdf;
  padding: 20px;
  border-radius: 5px;
  color: white;
  background-color: #003653 !important;
}

.LoginField > label,
.LoginField p {
  color: rgb(255, 255, 255) !important;
  background-color: #003653 !important;
}
.LoginContainer form button {
  background: #0563b4;
  border-radius: 0 !important;
}
.SignupField .warning {
  color: red !important;
  background-color: #003653 !important;
  width: 100%;
}

.LoginContainer form pre {
  font-size: large;
  background-color: #003653 !important;
  border: none;
}

.LoginContainer form h1,
.LoginContainer .field {
  background-color: #003653 !important;
  border: none;
}
.LoginContainer label {
  display: block;
  background-color: #003653 !important;
  border: none;
}
.LoginField input {
  background-color: #003653 !important;
  width: 100%;
  margin: 4vh 0;
  min-height: 3rem;
  color: white;
  border: none;
}

.SignupField {
  color: rgb(255, 255, 255) !important;
  background-color: #003653 !important;
}

.SignupContainer label {
  color: #ffffff !important;
  background-color: #003653 !important;
}

.LoginContainer .warning {
  color: red !important;
  background-color: #003653 !important;
  width: 100%;
  border: none;
}
.LoginForm,
.LoginField {
  background-color: #003653;
}
