body {
  background-color: #2fabf0 !important;
}
.navbar {
  margin: 0;
  width: 100%;
  max-width: 100%;
  height: 80px;
  padding: 0vw 10vw;
  background-color: #2fabf0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  text-decoration: none;
}
.navbar .link {
  color: white;
  font-size: 1.2rem;
}

.navbar .link:hover {
  color: black;
  font-size: 1.4rem;
}

.link img {
  max-height: 80px;
}

nav div {
  max-width: 60vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 50%;
}

nav button {
  width: 100px;
  font-size: 1.2rem;
  font-weight: 500;
  background-color: #0563b4;
  color: white;
  box-shadow: none;
  border-radius: 5px;
  border: none;
  padding: 5px 0px;
}
nav button:hover {
  cursor: pointer;
  background-color: #044278;
}

.navigationLinks {
  background-color: #2fabf0;
}
